@media only screen and (min-width: 768px){
    .rGrid {
        display: grid; grid-template-columns: repeat(8, 5vw); grid-template-rows: repeat(8, 5vw);
    }
    #rButton1 {
        align-self: center; position: relative; white-space: nowrap; padding-left: 15vw; padding-right: 15vw; margin-right: 0vw; font-size: 2vw; left: 10vw; transform: rotate(-90deg);
    }
    #rButton2 {
        align-self: center; position: relative; white-space: nowrap; padding-left: 15vw; padding-right: 15vw; margin-right: 0vw; font-size: 2vw; right: 10vw; transform: rotate(90deg);
    }
    #rCircle1 {
        font-size: 4vw; margin-top: 0.6vw; margin-left: 0.15vw;
    }
    #rCircle2 {
        font-size: 4vw; position: absolute; margin-top: 0.6vw; margin-left: -4.9vw;
    }
    #rCircle3 {
        font-size: 4vw; position: absolute; margin-top: 0.6vw; margin-left: 0.15vw;
    }
}

@media only screen and (max-width: 768px){
    .rGrid {
        display: grid; grid-template-columns: repeat(8, 10vw); grid-template-rows: repeat(8, 10vw);
    }
    #rButton1 {
        align-self: center; position: relative; white-space: nowrap; padding-left: 35vw; padding-right: 35vw; margin-right: 0vw; font-size: 2vw; left: 81.5vw; bottom: 45vw;
    }
    #rButton2 {
        align-self: center; position: relative; white-space: nowrap; padding-left: 35vw; padding-right: 35vw; margin-right: 0vw; font-size: 2vw; right: 81.5vw; top: 45vw;
    }
    #rCircle1 {
        font-size: 8vw; margin-top: 1.2vw; margin-left: 0.3vw;
    }
    #rCircle2 {
        font-size: 8vw; position: absolute; margin-top: 1.2vw; margin-left: -9.8vw;
    }
    #rCircle3 {
        font-size: 8vw; position: absolute; margin-top: 1.2vw; margin-left: 0.3vw;
    }
}